.Feature {
  position: absolute;

  opacity: 0;

  -webkit-animation-duration: .5s;

          animation-duration: .5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.Feature.left { -webkit-animation-name: appear-left; animation-name: appear-left; }
.Feature.right { -webkit-animation-name: appear-right; animation-name: appear-right; }

.Feature:nth-of-type(1) { -webkit-animation-delay: 1.5s; animation-delay: 1.5s; }
.Feature:nth-of-type(2) { -webkit-animation-delay: 1.7s; animation-delay: 1.7s; }
.Feature:nth-of-type(3) { -webkit-animation-delay: 1.9s; animation-delay: 1.9s; }
.Feature:nth-of-type(4) { -webkit-animation-delay: 2.1s; animation-delay: 2.1s; }

.Feature-line {
  position: absolute;
  top: -.0625em;

  width: 4em;
  height: .125em;

  background-color: #f12;
}

.Feature.left .Feature-line { right: 0; }
.Feature.right .Feature-line { left: 0; }

.Feature-line::before {
  content: '';

  position: absolute;
  top: -.5em;

  width: 1em;
  height: 1em;

  border-radius: 100%;

  background-color: #f12;
}

.Feature.left .Feature-line::before { right: -.5em; }
.Feature.right .Feature-line::before { left: -.5em; }

.Feature-content {
  position: absolute;
  top: -.5em;

  width: 16em;

  text-align: left;
}

.Feature.right .Feature-content { left: 5em; }
.Feature.left .Feature-content { right: 5em; }

.Feature-title {
  margin: 0 0 .25em;
}

.Feature-description {
  margin: .25em 0 0;
}

/* Appearance animation */
@-webkit-keyframes appear-left {
  from {
    opacity: 0;
    -webkit-transform: translate(-4em, 0);
            transform: translate(-4em, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}
@keyframes appear-left {
  from {
    opacity: 0;
    -webkit-transform: translate(-4em, 0);
            transform: translate(-4em, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

@-webkit-keyframes appear-right {
  from {
    opacity: 0;
    -webkit-transform: translate(4em, 0);
            transform: translate(4em, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

@keyframes appear-right {
  from {
    opacity: 0;
    -webkit-transform: translate(4em, 0);
            transform: translate(4em, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}



